:root {
  --main-ff: "RFDewiExtended", sans-serif;
  --color-dark: #2d3040;
  --color-white: #fff;
  --color-orange: #ef7d00;
  --color-orange-light: #ffe9ce;
  --color-purple-light: #f9f9ff;
  --color-comet: #5C6073;
  --color-dark-blue: #2E3458;
  --color-darker-blue: #242945;
  --color-grey: #C6C6C6;
}

@font-face {
  font-weight: 400;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Regular.woff2") format("woff2"), url("../fonts/RFDewiExtended-Regular.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Semibold.woff2") format("woff2"), url("../fonts/RFDewiExtended-Semibold.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Bold.woff2") format("woff2"), url("../fonts/RFDewiExtended-Bold.woff") format("woff");
}

@font-face {
  font-weight: 800;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Ultrabold.woff2") format("woff2"), url("../fonts/RFDewiExtended-Ultrabold.woff") format("woff");
}

@font-face {
  font-weight: 900;
  font-family: "RFDewiExtended";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RFDewiExtended-Black.woff2") format("woff2"), url("../fonts/RFDewiExtended-Black.woff") format("woff");
}

.button-reset {
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

.logo__img {
  display: block;
  width: 30rem;
}

@media screen and (max-width: 767px) {
  .logo__img {
    max-width: 15.4rem;
  }
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  margin-right: 2.8rem;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  font-size: 2rem;
  line-height: 3.8rem;
  transition: color 200ms;
}

.nav__link.is-current {
  color: var(--color-orange);
}

.nav__link:hover {
  color: var(--color-orange);
}

.button-burger {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 6.5rem;
  height: 6.5rem;
  cursor: pointer;
}

.button-burger span {
  display: block;
  margin-bottom: 1rem;
  width: 5.7rem;
  height: 2px;
  background-color: var(--color-dark);
  transition: background-color 200ms;
}

.button-burger span:last-child {
  margin-bottom: 0;
}

.button-burger:hover span {
  background-color: var(--color-orange);
}

.button-burger:active {
  transform: scale(0.99);
}

.tel__link {
  display: inline-block;
  font-weight: 800;
  font-size: 2.2rem;
  line-height: 3.6rem;
  transition: color 200ms;
}

.tel__link:hover {
  color: var(--color-orange);
}

.tel__text {
  display: block;
  font-size: 1.3rem;
  line-height: 1.6rem;
  color: #959FAB;
}

.section-name {
  display: block;
  font-weight: 600;
  font-size: 2.4rem;
  line-height: 2.9rem;
  color: rgba(45, 48, 64, 0.4);
}

@media screen and (max-width: 1199px) {
  .section-name {
    font-size: 2rem;
    line-height: 2.4rem;
  }
}

@media screen and (max-width: 767px) {
  .section-name {
    font-size: 1.4rem;
    line-height: 1.6rem;
  }
}

.site-second-title {
  font-weight: 800;
  font-size: 3.2rem;
  line-height: 3.8rem;
}

@media screen and (max-width: 1199px) {
  .site-second-title {
    font-size: 2.6rem;
    line-height: 3rem;
  }
}

@media screen and (max-width: 767px) {
  .site-second-title {
    font-size: 2.5rem;
    line-height: 3rem;
  }
}

.slider-nav {
  width: 10.8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media screen and (max-width: 767px) {
  .slider-nav {
    width: 7.8rem;
  }
}

.slider-nav__btn {
  width: 4.4rem;
  height: 4.4rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(45, 48, 64, 0.1);
  cursor: pointer;
  transition: background-color 200ms, border-color 200ms;
}

.slider-nav__btn:hover {
  border-color: var(--color-orange);
}

.slider-nav__btn:hover .slider-nav__btn-icon {
  fill: var(--color-orange);
}

.slider-nav__btn:active {
  transform: scale(0.9);
}

.slider-nav__btn.swiper-button-disabled {
  opacity: 0.5;
  pointer-events: none;
}

.slider-nav__btn-icon {
  display: block;
  width: 2rem;
  height: 2rem;
  fill: var(--color-dark);
  transition: fill 200ms;
}

@media screen and (max-width: 767px) {
  .slider-nav__btn {
    width: 3.5rem;
    height: 3.5rem;
  }
  .slider-nav__btn-icon {
    width: 1.5rem;
    height: 1.5rem;
  }
}

.button {
  display: inline-block;
  padding: 2.3rem 4.3rem;
  border-radius: 1.5rem;
  border: 2px solid var(--color-orange);
  color: var(--color-orange);
  font-weight: 900;
  font-size: 1.5rem;
  line-height: 2rem;
  text-align: center;
  letter-spacing: 0.11em;
  text-transform: uppercase;
  transition: color 200ms, background-color 200ms;
}

@media screen and (max-width: 767px) {
  .button {
    font-size: 1rem;
    line-height: 1.5rem;
    padding: 1.9rem 3.4rem;
  }
}

.button:hover {
  background-color: var(--color-orange);
  color: var(--color-white);
}

.button:active {
  transform: scale(0.99);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  line-height: 1.4;
}

@media screen and (min-width: 2100px) {
  html {
    font-size: 14px;
  }
}

@media screen and (max-width: 1536px) {
  html {
    font-size: 8px;
  }
}

@media screen and (max-width: 1366px) {
  html {
    font-size: 7px;
  }
}

@media screen and (max-width: 767px) {
  html {
    font-size: 10px;
  }
}

@media screen and (max-width: 360px) {
  html {
    font-size: 9px;
  }
}

@media screen and (max-width: 320px) {
  html {
    font-size: 8px;
  }
}

body {
  color: var(--color-dark);
  font-family: var(--main-ff);
  background-color: var(--color-white);
  font-size: 1.6rem;
  line-height: 1.9rem;
}

@media screen and (max-width: 767px) {
  body {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  color: inherit;
  text-decoration: none;
}

.list-reset {
  padding: 0;
  margin: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.site-container {
  width: 100%;
  max-width: 1680px;
  margin: 0 auto;
  padding: 0 32px;
}

@media screen and (min-width: 2100px) {
  .site-container {
    max-width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .site-container {
    padding: 0 20px;
  }
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
}

.header {
  padding-top: 2.4rem;
}

@media screen and (max-width: 767px) {
  .header {
    padding-top: 2.2rem;
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  margin-right: 10.9rem;
}

.header__nav {
  margin-right: 9.9rem;
}

@media screen and (max-width: 1199px) {
  .header__nav {
    display: none;
  }
}

@media screen and (max-width: 1199px) {
  .header__btn-burger {
    margin-left: auto;
  }
}

@media screen and (max-width: 767px) {
  .header__btn-burger {
    width: 4rem;
    height: 4rem;
  }
  .header__btn-burger span {
    width: 100%;
  }
}

.header__links {
  margin-left: auto;
}

@media screen and (max-width: 1199px) {
  .header__links {
    margin-left: 3rem;
  }
}

@media screen and (max-width: 767px) {
  .header__links {
    display: none;
  }
}

.footer {
  color: var(--color-white);
}

.footer__logo .logo__img {
  width: 21.9rem;
}

.footer__btn-top {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.footer__btn-top:hover ._icon-wrap {
  background-color: var(--color-orange);
}

.footer__btn-top:hover ._icon-wrap .footer__btn-top-icon {
  fill: var(--color-orange-light);
}

.footer__btn-top:hover ._name {
  color: var(--color-orange-light);
}

@media screen and (max-width: 991px) {
  .footer__btn-top {
    display: none;
  }
}

.footer__btn-top ._name {
  font-weight: 700;
  color: var(--color-orange);
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-transform: uppercase;
  transition: color 200ms;
}

.footer__btn-top ._icon-wrap {
  width: 5.8rem;
  height: 5.8rem;
  background-color: var(--color-orange-light);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1.5rem;
  transition: background-color 200ms;
}

.footer__btn-top-icon {
  width: 2.7rem;
  height: 2.7rem;
  fill: var(--color-orange);
  transition: fill 200ms;
}

.footer__content-text p {
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 2.9rem;
  max-width: 42.7rem;
}

@media screen and (max-width: 767px) {
  .footer__content-text p {
    font-size: 1.6rem;
    line-height: 1.9rem;
  }
}

.footer__tel .tel__link {
  font-size: 4.2rem;
  line-height: 4.9rem;
  letter-spacing: 0.02em;
  color: var(--color-white);
  margin-bottom: 0.9rem;
}

.footer__tel .tel__link:hover {
  color: var(--color-orange);
}

@media screen and (max-width: 767px) {
  .footer__tel .tel__link {
    font-size: 2.5rem;
    line-height: 3rem;
    letter-spacing: 0.02em;
    margin-bottom: 0.4rem;
  }
}

.footer__tel .tel__text {
  font-size: 1.6rem;
  line-height: 1.9rem;
}

@media screen and (max-width: 767px) {
  .footer__tel .tel__text {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}

.footer__mail-link {
  position: relative;
  display: inline-block;
  font-weight: 800;
  font-size: 4.2rem;
  line-height: 4.9rem;
  letter-spacing: 0.02em;
  color: var(--color-white);
  transition: color 200ms;
}

.footer__mail-link:hover {
  color: var(--color-orange);
}

.footer__mail-link:hover::before {
  opacity: 0;
}

.footer__mail-link::before {
  position: absolute;
  left: 0;
  bottom: -1.1rem;
  width: 100%;
  height: 1px;
  background-color: var(--color-white);
  opacity: 0.3;
  transition: opacity 200ms;
  content: "";
}

@media screen and (max-width: 767px) {
  .footer__mail-link::before {
    bottom: -0.3rem;
  }
}

@media screen and (max-width: 767px) {
  .footer__mail-link {
    font-size: 2.5rem;
    line-height: 3rem;
    letter-spacing: 0.02em;
  }
}

.footer__legend {
  display: block;
  margin-bottom: 4rem;
  font-size: 1.3rem;
  line-height: 1.5rem;
  color: var(--color-grey);
}

@media screen and (max-width: 767px) {
  .footer__legend {
    margin-bottom: 2rem;
  }
}

.footer__nav {
  display: flex;
  flex-wrap: wrap;
  max-width: 40.8rem;
  margin: -1.2rem;
}

@media screen and (max-width: 1536px) {
  .footer__nav {
    max-width: 47.8rem;
  }
}

@media screen and (max-width: 1440px) {
  .footer__nav {
    max-width: 43.8rem;
  }
}

@media screen and (max-width: 1366px) {
  .footer__nav {
    max-width: 46.8rem;
  }
}

@media screen and (max-width: 1199px) {
  .footer__nav {
    max-width: 34.8rem;
  }
}

@media screen and (max-width: 767px) {
  .footer__nav {
    margin: -1rem;
    max-width: 100%;
  }
}

.footer__nav-item {
  width: calc(50% - 2.4rem);
  margin: 1.2rem;
}

@media screen and (max-width: 767px) {
  .footer__nav-item {
    width: calc(50% - 2rem);
    margin: 1rem;
  }
}

.footer__nav-link {
  font-weight: 700;
  transition: color 200ms;
}

.footer__nav-link:hover {
  color: var(--color-orange);
}

@media screen and (max-width: 767px) {
  .footer__nav-link {
    margin-bottom: 2rem;
    font-size: 1.4rem;
    line-height: 1.7rem;
  }
}

.footer__feautures {
  display: block;
  max-width: 28.7rem;
  margin-bottom: 2.2rem;
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 1.9rem;
}

.footer__feautures._fw400 {
  font-weight: 400;
}

@media screen and (max-width: 767px) {
  .footer__feautures {
    margin-bottom: 2rem;
    font-size: 1.4rem;
    line-height: 1.6rem;
  }
}

.footer__top-block {
  background-color: var(--color-dark-blue);
  padding: 4.5rem 0 5.8rem 0;
}

.footer__top-block-info {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  column-gap: 20px;
}

@media screen and (max-width: 767px) {
  .footer__top-block-info {
    column-gap: 0;
  }
}

@media screen and (max-width: 991px) {
  .footer__top-block-info {
    row-gap: 3.2rem;
  }
}

.footer__top-block-info ._block:nth-child(1) {
  grid-column: 6 span;
}

@media screen and (max-width: 991px) {
  .footer__top-block-info ._block:nth-child(1) {
    grid-column: 16 span;
  }
}

.footer__top-block-info ._block:nth-child(2) {
  grid-column: 5 span;
}

@media screen and (max-width: 991px) {
  .footer__top-block-info ._block:nth-child(2) {
    grid-column: 8 span;
  }
}

@media screen and (max-width: 767px) {
  .footer__top-block-info ._block:nth-child(2) {
    grid-column: 16 span;
  }
}

.footer__top-block-info ._block:nth-child(3) {
  grid-column: 5 span;
}

@media screen and (max-width: 767px) {
  .footer__top-block-info ._block:nth-child(3) {
    grid-column: 16 span;
  }
}

@media screen and (max-width: 767px) {
  .footer__top-block {
    padding: 3rem 0 4rem;
  }
}

.footer__top-block-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 7.8rem;
}

@media screen and (max-width: 991px) {
  .footer__top-block-wrapper {
    margin-bottom: 4rem;
  }
}

.footer__bottom-block {
  padding-top: 10.6rem;
  padding-bottom: 3.6rem;
  background-color: var(--color-darker-blue);
}

@media screen and (max-width: 767px) {
  .footer__bottom-block {
    padding-top: 4.2rem;
    padding-bottom: 5.3rem;
  }
}

.footer__bottom-block-content-copyright {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  column-gap: 20px;
  margin-top: 12.8rem;
}

@media screen and (max-width: 767px) {
  .footer__bottom-block-content-copyright {
    margin-top: 2.8rem;
  }
}

.footer__bottom-block-content-copyright ._block:nth-child(1) {
  grid-column: 2 span;
}

@media screen and (max-width: 991px) {
  .footer__bottom-block-content-copyright ._block:nth-child(1) {
    grid-column: 6 span;
  }
}

@media screen and (max-width: 767px) {
  .footer__bottom-block-content-copyright ._block:nth-child(1) {
    grid-column: 16 span;
    margin-bottom: 0.8rem;
  }
}

.footer__bottom-block-content-copyright ._block:nth-child(2) {
  grid-column: 4 span;
}

@media screen and (max-width: 991px) {
  .footer__bottom-block-content-copyright ._block:nth-child(2) {
    grid-column: 5 span;
  }
}

@media screen and (max-width: 767px) {
  .footer__bottom-block-content-copyright ._block:nth-child(2) {
    grid-column: 16 span;
    margin-bottom: 3.4rem;
  }
}

.footer__bottom-block-content-copyright ._block:nth-child(3) {
  grid-column: 10 span;
}

@media screen and (max-width: 991px) {
  .footer__bottom-block-content-copyright ._block:nth-child(3) {
    grid-column: 5 span;
  }
}

@media screen and (max-width: 767px) {
  .footer__bottom-block-content-copyright ._block:nth-child(3) {
    grid-column: 16 span;
  }
}

.footer__bottom-block-content-copyright ._block p {
  display: inline-flex;
  font-size: 1.3rem;
  line-height: 1.6rem;
  color: var(--color-grey);
}

.footer__bottom-block-content-copyright ._block a {
  font-size: 1.3rem;
  line-height: 1.6rem;
  color: var(--color-grey);
  display: inline-flex;
  align-items: center;
  transition: color 200ms;
}

.footer__bottom-block-content-copyright ._block a img {
  margin-left: 0.7rem;
}

.footer__bottom-block-content-copyright ._block a:hover {
  color: var(--color-orange);
}

.footer__bottom-block-content {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  column-gap: 20px;
}

@media screen and (max-width: 767px) {
  .footer__bottom-block-content {
    row-gap: 40px;
  }
}

.footer__bottom-block-content ._block:nth-child(1) {
  grid-column: 6 span;
}

@media screen and (max-width: 767px) {
  .footer__bottom-block-content ._block:nth-child(1) {
    grid-column: 16 span;
  }
}

.footer__bottom-block-content ._block:nth-child(2) {
  grid-column: 5 span;
}

@media screen and (max-width: 767px) {
  .footer__bottom-block-content ._block:nth-child(2) {
    grid-column: 16 span;
  }
}

.footer__bottom-block-content ._block:nth-child(3) {
  grid-column: 5 span;
}

@media screen and (max-width: 767px) {
  .footer__bottom-block-content ._block:nth-child(3) {
    grid-column: 16 span;
  }
}

.hero {
  padding-top: 14.4rem;
  padding-bottom: 20rem;
}

.hero__slider-img {
  position: absolute;
  top: 7rem;
  right: 3.6rem;
  width: 107.3rem;
  height: 78.8rem;
}

@media screen and (max-width: 1199px) {
  .hero__slider-img {
    width: 77.3rem;
    height: 58.8rem;
  }
}

@media screen and (max-width: 991px) {
  .hero__slider-img {
    top: 11rem;
    right: -8.4rem;
  }
}

@media screen and (max-width: 767px) {
  .hero__slider-img {
    position: static;
    top: auto;
    right: auto;
    width: auto;
    height: auto;
    transform: translateX(9rem);
  }
}

@media screen and (max-width: 480px) {
  .hero__slider-img {
    width: 40.2rem;
    height: 29.5rem;
    transform: translateX(2rem);
  }
}

.hero__img {
  width: 100%;
  height: 100%;
}

.hero__slider-warpper {
  position: relative;
  z-index: 2;
}

@media screen and (max-width: 767px) {
  .hero__slider-warpper {
    flex-direction: column;
    margin-top: -5rem;
  }
}

.hero__content-text p {
  font-weight: 600;
  font-size: 1.8rem;
  line-height: 2.2rem;
}

@media screen and (max-width: 767px) {
  .hero__imgs-wrapper {
    position: relative;
    margin-top: -5rem;
  }
}

@media screen and (max-width: 480px) {
  .hero__imgs-wrapper {
    margin-top: -4rem;
  }
}

.hero__rec {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  width: 113.6rem;
  height: 86.3rem;
}

@media screen and (max-width: 1199px) {
  .hero__rec {
    width: 87.3rem;
    height: 68.8rem;
  }
}

@media screen and (max-width: 991px) {
  .hero__rec {
    top: 1rem;
    right: -13rem;
  }
