    font-weight: 600;
    font-size: 0.9rem;
    line-height: 1.6rem;
    margin: 0.2rem;
  }
}

.projects-slider__info p {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 1.6rem;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #5C6073;
}

@media screen and (max-width: 991px) {
  .projects-slider__info p {
    max-width: 29rem;
  }
}

@media screen and (max-width: 767px) {
  .projects-slider__info p {
    font-size: 1.1rem;
    line-height: 1.4rem;
    max-width: 13.7rem;
  }
}

.projects-slider__legend {
  display: inline-block;
  margin-bottom: 1.4rem;
  font-weight: 700;
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-transform: uppercase;
}

@media screen and (max-width: 767px) {
  .projects-slider__legend {
    font-size: 0.9rem;
    line-height: 1.6rem;
    margin-bottom: 0.4rem;
  }
}
